.title-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    padding-right: 1vw;
    padding: 5px 0px 5px 0px;

    /* border: 1px solid red; */
}

.title {
    color: #004494;
    font-size: 1.2vw;
    font-weight: 500;
}

.title p {
    margin: 0;
}

.settings {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.download-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: auto;
    width: auto;
    padding: 5px;
    gap: 10px;
    color: #404040;
    cursor: pointer;
}

.download-section p {
    font-size: 80%;
    margin: 0;
}

.download-icon {
    width: 15px;
    height: 15px;
    stroke: #404040;
}

.currency-selector {
    font-family: inherit;
    font-size: 0.9vw;
    color: #404040;
    margin: 0vw 1.5vw 0vw 1.5vw;
    border: none;
    cursor: pointer;
}