.row-box {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(10px, auto);
    background-color: #EBEBEB;
    gap: 50px;
    box-sizing: border-box;
    
    grid-column: 1 / -1;
    width: 100%;
    overflow-x: hidden;

    padding-left: 30px;
    padding-right: 30px;
}

.row-box-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.row-box-title {
    font-size: 1.1em;
    font-weight: 500;
    color: #003776;
    margin-top: 0.8em;
    margin-bottom: 0.8em;
}