.card-medium {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 7px;
    width: 100%;
    height: 350px;
    min-width: 0;
    /* margin-bottom: 0.5%; */
    /* margin: 5px 15px 5px 15px; */
    grid-column: span 2;
}

.card-medium-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: auto;
    height: auto;
    border-bottom: 1px solid #EBEBEB;
    padding: 0.5vw 0 0.5vw 0.5vw;
    position: relative;
}

.card-medium-title {
    margin: 0;
    font-size: 1.05vw;
    font-weight: 500;
    color: #003776;
}

.card-medium-subtitle {
    margin: 0;
    font-size: 0.9vw;
}

.card-medium-header svg {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 3px;
    margin-right: 3px;
    width: 18px;
    height: 18px;
    fill: none;
    cursor: pointer;

    /* border: 1px solid red; */
}

.chart-medium {
    /* width: auto; */
    /* height: 280; */
    border-radius: 7px;
    overflow: hidden;

    /* border: 1px solid red; */
}