.filters {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    gap: 15px;
    background-color: #EBEBEB;
    padding: 0px 0px 3px 0px;
    height: 45px;
    grid-column: 1 / -1;
    grid-row: 1;
}

.f-title {
    font-size: 1.05vw;
    font-weight: 500;
    color: #003776;
    margin: 0px 0px;
}

.filter-logo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: fit-content;
    gap: 5px;
}

.filter-logo > svg {
    width: 20px;
    height: 20px;
}

.filter-logo > p {
    font-size: 0.8em;
}

.filter-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 3px;
    width: 180px;
}

.fbox-selector {
    width: 180px;
    border: none;
    background-color: #ffffff;
}

.fbox-label {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: 0.8em;
}

.filter-btn {
    font-size: 0.85em;
    border: 1px solid #707070;
    border-radius: 3px;
    padding: 1px 3px 1px 3px;
    cursor: pointer;
    color: #000000;
    background-color: #EBEBEB;
}