.kpi-card-small {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: #BFD0E4;
    border-radius: 7px;
    /* width: 23%; */
    width: 100%;
    min-width: 0;
    height: 100px;
    aspect-ratio: 3 / 1;
    margin-bottom: 0.5%;
}

.label {
    font-size: 0.9vw;
    margin: 0;
    text-align: center;
}

.value {
    font-size: 1.3vw;
    font-weight: 650;
    color: #003776;
    margin: 0;
}

.info {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 5px;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    padding: 0;
    cursor: pointer;
}

.info svg {
    fill: none;
}

