.sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
    position: fixed;
    top: 45px;
    left: 0;
    /* margin-top: 45px; */
    width: 215px;
    height: calc(100vh - 45px);
    gap: 5px;
    overflow-y: auto;

    /* border: 1px solid red; */
}

.menu-title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 90%;
    font-weight:100;
    padding: 2vw 0 1.5vw 0;
}

.menu-title p {
    margin: 0;
    color: #003776;
    font-size: 1.3vw;
    font-weight: 500;
}

.menu-item {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
    margin-bottom: 0.5vw;
    width: 95%;
    gap: 5px;
    padding: 3px 0px 3px 3px;
}

/* .menu-item:hover {
    cursor: pointer;
} */

.text-label {
    display: flex;
    flex-direction: column;
    width: 80%;
}

.text-label  p {
    font-size: smaller;
    margin: 0;
}

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 13%;
    fill: none;
}

.logo svg {
    width: auto;
    height: auto;
    aspect-ratio: 1 / 1;
}

.logo path {
    /* Controls the SVG logos from the Sidebar Menu */
    /* fill: red; */
    stroke: black;
    /* stroke-width: 1; */
}

/* OPEN STATE */
.open,
.open .logo path {
    background-color: #003776;
    color: white;
    stroke: white;
}