.persoane-sprijinite {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.ps-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 40px;
}

.ps-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1.5em;
}

.ps-value {
    margin: 0;
    color: #003776;
    font-size: 1.4em;
    font-weight: bold;
}

.ps-label {
    font-size: 0.9em;
}