.header {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    width: 100%;
    height: 45px;
    gap: 10px;
    border-bottom: 2px solid #EBEBEB;
    padding: 0px, 0px, 0px, 0px;
    
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    background: white;      /* optional for visibility */
}