.card {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 7px;
    width: 100%;
    height: 350px;
    min-width: 0;
    margin-bottom: 0.5%;
}

.card-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: auto;
    height: auto;
    border-bottom: 1px solid #EBEBEB;
    padding: 0.5em 0em 0.5em 0.5em;
    position: relative;
}

.card-title {
    margin: 0;
    font-size: 1.05vw;
    font-weight: 500;
    color: #003776;
}

.card-subtitle {
    margin: 0;
    font-size: 0.9vw;
}

.card-header svg {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 5px;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    fill: none;
}

.chart {
    /* width: auto; */
    /* height: 280; */
    border-radius: 7px;
    overflow: hidden;

    /* border: 1px solid red; */
}