.main {
    background-color: #EBEBEB;
    padding: 10px;
    gap: 10px;
    height: fit-content;
    min-height: calc(100vh - 45px);
    box-sizing: border-box;

    margin-left: 215px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(10px, auto);

    max-width: calc(100vw - 215px);
    overflow-x: hidden;
    overflow-y: auto;
    flex-grow: 1;
}