.all-icons {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #EBEBEB;
    width: fit-content;
    margin: 10px 0px 10px 0px;
}

.svg-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #BFD0E4;
    margin: 5px 5px 5px 5px;
    width: 100px;
    height: 100px;
}

.svg-icon svg {
    width: 55%;   /* scale down to fit nicely */
    height: 55%;
    display: block;
}
