.kpi-card-big {
    display: flex;
    flex-direction: column;
    align-items: left;
    position: relative;
    background-color: white;
    border-radius: 7px;
    width: 100%;
    height: 350px;
}

.kpi-big-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: auto;
    height: auto;
    border-bottom: 1px solid #EBEBEB;
    padding: 0.5em 0em 0.5em 0.5em;
    margin-bottom: 25px;
    position: relative;
}

.kpi-big-title {
    margin: 0;
    font-size: 1.05vw;
    font-weight: 500;
    color: #003776;
}

.kpi-big-subtitle {
    margin: 0;
    font-size: 0.9vw;
}

.bottom-section {
    display: flex;
    flex-direction: column;
    border-radius: 7px;
    background-color: #BFD0E4;
    width: 80%;
    margin-top: 10%;
    margin-bottom: 5%;
    margin-left: auto;
    margin-right: auto;
    padding: 3%;
}

.kpi-big {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    gap: 5%;
    padding-left: 15%;
    padding-top: 3%;
}

.kpi-value {
    font-size: 1.3vw;
    font-weight: 650;
    color: #003776;
    margin: 0;
}

.kpi-label {
    font-size: 0.9vw;
    margin: 0;
    text-align: center;
}

.bottom-value {
    font-size: 1.3vw;
    font-weight: 650;
    color: #003776;
    margin: 0;
    text-align: center;
}

.bottom-label {
    font-size: 0.9vw;
    margin: 0;
    text-align: center;
}

.info {
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 2%;
    padding-right: 2%;
    width: 7%;
    fill: none;
    cursor: pointer;
}

.info {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    margin-top: 5px;
    margin-right: 5px;
    width: 20px;
    height: 20px;
    padding: 0;
    fill: none;
    cursor: pointer;
}

.info > svg {
    fill: none;
}

.home-icon {
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
}