.accordion {
    display: flex;
    flex-direction: column;
    width: 700px;
    margin: 100px auto;
    gap: 20px;
    grid-column: 1 / -1;
}

.acc-item {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
    padding: 15px 35px 15px 24px;
    cursor: pointer;
    background-color: #fff;
    color: #707070;
    border-top: 1px solid #9F9F9F;
    border-right: 1px solid #9F9F9F;
    border-radius: 7px;

    display: grid;
    grid-template-columns: auto 1fr auto;
    column-gap: 24px;
    row-gap: 32px;
    align-items: center;
}

.acc-number {
    font-size: 24px;
    font-weight: 500;
    color: #ced4da;
    margin: 0px;
}

.acc-title,
.acc-icon {
    font-size: 24px;
    font-weight: 500;
    margin: 0px;
}

.content-box {
    grid-column: 2 / -1;
    padding-bottom: 16px;
    line-height: 1.6;
    background-color: #fff;
    color: black;
}

.content-box ul {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-left: 16px;
    margin-top: 16px;
    list-style-type: square;
    padding-left: 30px;
    
}

.content-box ol {
    list-style-position: outside;
    padding-left: 30px;
}

.content-box p {
    padding-top: 15px;
}

/* OPEN STATE */
.acc-open {
    border-top: 4px solid #003776;
    border-right: none;
}

.acc-open .acc-number {
    color: #4073AF;
}

.acc-open .acc-text {
    color: #003776;
}
